.test {
  background: red;
}

.test .wrap {
  max-width: 1200px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.test .wrap .row {
  margin-left: -15px;
  margin-right: -15px;
}

.test .wrap .row::after {
  display: block;
  content: "";
  clear: both;
}

.test .wrap .row .item {
  float: left;
  padding-left: 15px;
  padding-right: 15px;
  word-wrap: break-word;
  width: calc(25% - 30px);
  overflow: hidden;
}

.icon.archives {
  font-size: 5px;
  text-align: center;
}

.icon.archives:before {
  width: 53em;
  height: 52.5em;
  background-position: 1.5em -56.7em;
}

.content {
  color: red;
  font-size: 25px;
}

@media screen and (max-width: 1100px) {
  .test .wrap {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 960px) {
  .test .wrap {
    padding-left: 15px;
    padding-right: 15px;
  }

  .test .wrap .row .item {
    width: calc(33.333333333333336% - 30px);
  }
}

@media screen and (max-width: 780px) {
  .test .wrap {
    padding-left: 15px;
    padding-right: 15px;
  }

  .test .wrap .row .item {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 560px) {
  .test .wrap {
    padding-left: 15px;
    padding-right: 15px;
  }

  .test .wrap .row .item {
    width: calc(100% - 30px);
  }
}