$columns: 12;
$offset: 30px;
$offset_one_side: ($offset / 2);
$atom: 8.333333333333334%;

$break_lg: 1100px;
$break_md: 960px;
$break_sm: 780px;
$break_xs: 560px;

@mixin reset() {
    body,div,dl,dt,dd,ul,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,input,textarea,p,blockquote,th,td {
        margin: 0;
        padding: 0; }

    table {
        border-collapse: collapse;
        border-spacing: 0; }

    fieldset,img,abbr {
        border: 0; }

    address,caption,cite,code,dfn,em,strong,th,var {
        font-style: normal;
        font-weight: normal; }

    ul li {
        list-style: none; }

    caption,th {
        text-align: left; }

    h1,h2,h3,h4,h5,h6 {
        font-size: 100%;
        font-weight: normal; }

    sup {
        vertical-align: text-top; }

    sub {
        vertical-align: text-bottom; }

    input,textarea,select {
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit; }

    legend {
        color: #000; }

    article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section,main {
        display: block; } }



@mixin wrapper() {
    max-width: 1200px;
    padding-left: 30px;
    padding-right: 30px;
    margin: 0 auto;

    @media screen and (max-width: $break_lg) {
        padding-left: 30px;
        padding-right: 30px; }


    @media screen and (max-width: $break_md) {
        padding-left: 15px;
        padding-right: 15px; }


    @media screen and (max-width: $break_sm) {
        padding-left: 15px;
        padding-right: 15px; }


    @media screen and (max-width: $break_xs) {
        padding-left: 15px;
        padding-right: 15px; } }


@mixin row-flex() {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px; }

@mixin row-float() {
    margin-left: -15px;
    margin-right: -15px;
    @include clearfix(); }

@mixin col() {
    box-sizing: border-box;
    margin-left: 15px;
    margin-right: 15px;
    word-wrap: break-word; }

@mixin col-float() {
    float: left; }

@mixin col-padding() {
